/**
 * mathjs toFixed
 * @param {*} d
 */
Number.prototype.toFixed = function(d) {
  return mathjs.format(this, { notation: "fixed", precision: d });
};

//string replaceAll
String.prototype.replaceAll = function(s1, s2) {
  return this.replace(new RegExp(s1, "gm"), s2);
};

/** */
let getBrowserType = () => {
  //if
  if (globalThis.navigator.userAgent.includes("Trident")) {
    return "ie";
  } else if (globalThis.navigator.userAgent.includes("Edge")) {
    return "edge";
  } else if (globalThis.navigator.userAgent.includes("Firefox")) {
    return "firefox";
  } else if (globalThis.navigator.userAgent.includes("Chrome")) {
    return "chrome";
  } else if (globalThis.navigator.userAgent.includes("Safari")) {
    return "safari";
  } else {
    return "other";
  }
};

let downloadFile = function(data) {
  let url = data.path || data.url;
  let isChrome = getBrowserType() == "chrome";
  let isSafari = getBrowserType() == "safari";
  let isFirefox = getBrowserType() == "firefox";

  if (!url) {
    console.error("url", data, url);
  }

  //iOS devices do not support downloading. We have to inform user about this.
  if (/(iP)/g.test(navigator.userAgent)) {
    alert(
      "Your device does not support files downloading. Please try again in desktop browser."
    );
    return false;
  }

  var link = document.createElement("a");
  link.href = url;
  link.target = "_blank";

  if (link.download !== undefined) {
    var fileName = url.substring(url.lastIndexOf("/") + 1, url.length);
    link.download = fileName;
  }

  //Dispatching click event.  download via virtual link click
  if (document.createEvent) {
    var e = document.createEvent("MouseEvents");
    e.initEvent("click", true, true);
    link.dispatchEvent(e);
  } else {
    if (!url.includes("?")) {
      url += "?download";
    }
    globalThis.open(url, "_self");
  }

  // 
  fetch
    .post("/operation/downloadlog", {
      object: data.object,
      item: data.item,
      path: url
    })
    .then(res => {
      console.log("");
    })
    .catch(err => {
      console.error("", err.message);
    });

  return true;
};

/**
 * @param {*} number 
 * @param {*} places 
 * @param {*} symbol   $
 * @param {*} thousand    ,
 * @param {*} decimal    .
 */
let formatMoney = function(number, places, symbol, thousand, decimal) {
  number = number || 0;
  places = !isNaN((places = Math.abs(places))) ? places : 2;
  symbol = symbol ?? "";
  thousand = thousand || ",";
  decimal = decimal || ".";
  var negative = number < 0 ? "-" : "",
    i = parseInt((number = Math.abs(+number || 0).toFixed(places)), 10) + "",
    j = (j = i.length) > 3 ? j % 3 : 0;

  let digit =
    (j ? i.substr(0, j) + thousand : "") +
    i.substr(j).replace(/(\d{3})(?=\d)/g, "$1" + thousand) +
    (places
      ? decimal +
        Math.abs(number - i)
          .toFixed(places)
          .slice(2)
      : "");

  if (Number(negative + digit) === 0) {
    negative = ""; // -0.00.0
  }

  return symbol + negative + digit;
};

