let getStringByteLength = str => {
  let len = 0;
  let regex = /[^\u0000-\u00FF]/; //  
  for (let i = 0; i < str.length; i++) {
    if (regex.test(str[i])) {
      len += 2;
    } else {
      len += 1;
    }
  }
  return len;
};

/**object  {}  */
let isObjectNull = obj => {
  if (typeof obj == "object") {
    if (obj === null) {
      return true;
    }
    if (Array.isArray(obj) && obj.length == 0) {
      return true;
    }

    let keys = Object.keys(obj);
    if (keys.length == 0) {
      return true;
    } else {
      return false;
    }
  } else {
    return true;
  }
};

/**
 *   
 * html@
 *
 * $on("",function)
 */
let dispatch = function(componentName, eventName, params) {
  var parent = this.$parent || this.$root;
  var name = parent.$options.componentName;

  //
  while (parent && (!name || name !== componentName)) {
    parent = parent.$parent;

    if (parent) {
      name = parent.$options.componentName;
    }
  }
  //jQueryclosest
  if (parent) {
    parent.$emit.apply(parent, [eventName].concat(params));
  }
};

/**
 *   
    $on("",function)
 */
let broadcast = function(componentName, eventName, params) {
  broadcast.call(this, componentName, eventName, params);
  function broadcast(componentName, eventName, params) {
    //
    this.$children.forEach(child => {
      var name = child.$options.componentName;
      //
      if (name === componentName) {
        //
        child.$emit.apply(child, [eventName].concat(params));
      } else {
        //
        broadcast.apply(child, [componentName, eventName].concat([params]));
      }
    });
  }
};

/**
 *  dom windowcsswidth/height
 */
let detectDomResize = function(element, callback) {
  let erd = elementResizeDetectorMaker();
  erd.listenTo(element, callback);
};

let uploadHttpRequest = params => {
  // const CancelToken = fetch.CancelToken;

  // promise on-success
  return new Promise((resolve, reject) => {
    let action = params.action; //url
    let file = params.file; //
    let data = params.data; // 

    //
    let formData = new FormData();
    formData.append("file", file);
    formData.append("data", JSON.stringify(data));

    fetch
      .post(action, formData, {
        headers: {
          "Content-Type": "multipart/form-data"
        },
        onUploadProgress: function(event) {
          let percent = (event.loaded / event.total) * 100;
          params.onProgress({
            percent: Math.floor(percent * 100) / 100,
            total: event.total
          });
        }
      })
      .then(res => {
        if (res.ret == 0) {
          resolve(res.data);
        } else {
          reject(new Error(res.msg));
        }
      })
      .catch(err => {
        reject(new Error(err.message));
      });
  });
};

let isXls = file => {
  let xlsMap = [
    "application/vnd.ms-excel",
    "application/vnd.openxmlformats-officedocument.spreadsheetml.sheet",
    "application/wps-office.xlsx",
    "application/x-xls",
    "application/-excel"
  ];

  let has = xlsMap.find(item => item == file.type);
  if (has) {
    return true;
  } else {
    return false;
  }
};

//route query
let encodeQueryString = object => {
  return encodeURIComponent(JSON.stringify(object));
};
// route query
let decodeQueryString = str => {
  return JSON.parse(decodeURIComponent(str));
};

// id    arr1=[1,2,3]  arr2=[2,3,4]  1,4
let diffArray = (arr1, arr2) => {
  return arr1.concat(arr2).filter(function(v, i, arr) {
    return arr.indexOf(v) === arr.lastIndexOf(v);
  });
};

